import React, { useContext } from 'react';

import { AnimatedList, ChatInput } from '..';
import './Chat.scss';
import { MessageContext } from '../../contexts/messages';
const Chat: React.FC = () => {
    const { push, messages, isFetching } = useContext(MessageContext);

    return (
        <div className="chat">
            <ChatInput push={push} />
            <AnimatedList messages={messages} isFetching={isFetching} />
        </div>
    );
};

export default Chat;
