import React, { useState, useContext } from 'react';
import { Route, Switch } from 'react-router-dom';
import posed, { PoseGroup } from 'react-pose';
import { useTheme } from '../../hooks';
import { ToggleButton, Chat } from '../';
import { MessageContext } from '../../contexts/messages';
import './App.scss';
import Info from '../Info/Info';
const RouteContainer = posed.div({
    enter: { opacity: 1 },
    exit: { opacity: 0 },
});
const App: React.FC = () => {
    const { theme, Themes, toggleTheme } = useTheme();
    const { name } = useContext(MessageContext);
    const [expanded, setExpanded] = useState(false);
    const toggleInfo = () => {
        setExpanded(!expanded);
    };
    return (
        <div className={theme === Themes.dark ? 'theme-dark' : 'theme-light'}>
            <div className="container">
                <span className="notch" />
                <div className="welcome">
                    <h1>Real-time chat</h1>
                    <div className="divider">
                        <span className="hr" />
                        <ToggleButton
                            checked={expanded}
                            toggle={toggleInfo}
                            checkedIcon="arrow"
                            uncheckedIcon="info"
                        />
                    </div>
                    <p>
                        Hi! From now your name is <span className="name">{name}</span> 😊
                    </p>
                    <p>(Open another window to see this real-time chat working)</p>
                </div>
                <Route
                    render={({ location }) => (
                        <PoseGroup>
                            <RouteContainer className="routerContainer" key={'routerContainer'}>
                                <Switch location={location} key={'switch'}>
                                    <Route path="/chat" component={Chat} key="chat" />
                                    <Route path="/info" component={Info} key="info" />
                                    <Route component={Chat} key="chat" />
                                </Switch>
                            </RouteContainer>
                        </PoseGroup>
                    )}
                />
                <div className="footer">
                    <ToggleButton
                        checked={theme === Themes.light}
                        toggle={toggleTheme}
                        checkedIcon="sun"
                        uncheckedIcon="moon"
                        big
                    />
                </div>
            </div>
        </div>
    );
};

export default App;
