import React, { useState, useRef, useEffect, useContext } from 'react';
import posed, { PoseGroup } from 'react-pose';
import { Message, MessageContext } from '../../contexts/messages';
import moment from 'moment';
import './AnimatedList.scss';

interface Props {
    isFetching: boolean;
    messages: Array<Message>;
}
const List = posed.ul({
    exit: { opacity: 0 },
    enter: {
        opacity: 1,
        beforeChildren: true,
        staggerChildren: 500,
    },
});
const AnimatedItem = posed.a({
    exit: { opacity: 0 },
    enter: { opacity: 1 },
});

const AnimatedList: React.FC<Props> = ({ messages, isFetching }) => {
    const [selected, setSelected] = useState<string>();
    const { name } = useContext(MessageContext);
    const listRef = useRef(null);

    useEffect(() => {
        console.log('...');
        if (window && listRef && listRef.current) window.scrollTo(0, listRef.current.offsetTop);
    }, [messages]);

    return (
        <div className="animatedList">
            {isFetching ? (
                <List ref={listRef} initialPose="exit" pose={'enter'}>
                    <PoseGroup>
                        {messages.map((message: Message, index: number) => {
                            return (
                                <AnimatedItem
                                    className={`message ${name === message.user ? 'own' : ''}`}
                                    key={`message${index}`}>
                                    <div className={`content `}>
                                        <p className="header">
                                            <span className="user">{message.user}</span>
                                            <span className="date">
                                                {moment(message.date).format('lll')}
                                            </span>
                                        </p>
                                        <p className="body">{message.message}</p>
                                    </div>
                                </AnimatedItem>
                            );
                        })}
                    </PoseGroup>
                </List>
            ) : (
                <p>fetching</p>
            )}
        </div>
    );
};

export default AnimatedList;
